import React from 'react';
import './About.css';

const About = () => {
    return(
        <div className="about">
            <h3 className="title">About Me</h3>
            <div className="bio">
                <p>Fiatal mindenre elsznt programoz vagyok. Szeretek j dolgokat tanulni s kiprblni magam klnbz terleteken. Fontosnak 
                tartom a folyamatos fejldst. Kreatvits s design miatt kedveltem meg a webfejlesztst. Ezt az oldalt is sajt magam 
                fejlesztsre, illetve referenciaknt ksztettem el Reactban. Clom, hogy sikeres legyek a szakmmban s ezltal biztostani 
                tudjak a csaldomnak egy szp letel.</p>
            </div>
        </div>
    );
}

export default About;


